import React, { useState } from "react";
import { useRecoilState, useSetRecoilState, useRecoilValue } from "recoil";
import { Box, Flex, Spacer, Checkbox, useToast } from "@chakra-ui/react";
import {
  NameState,
  DoingState,
  BacklogState,
  TodoState,
  DoneState,
} from "./atom";

interface todoProps {
  name: string;
  job: {
    id: number;
    title: string;
    owner: string;
  };
}

function List({ job, name }: todoProps) {
  const [checked, setchecked] = useState(false);
  let setBacklog = useSetRecoilState(BacklogState);
  let [Todo, setTodo] = useRecoilState(TodoState);
  let [Doing, setDoing] = useRecoilState(DoingState);
  let [Done, setDone] = useRecoilState(DoneState);
  let Name = useRecoilValue(NameState);

  const toast = useToast();

  const ClickList = async (id: number) => {
    if (checked === false) {
      let newTodo = {
        id: 0,
        title: "",
        owner: "",
      };
      if (name === "Backlog") {
        //   Backlog 

        setBacklog((backlog) => {
          let newBacklog = backlog.map(
            (back) => (back.id === id ? { ...back, owner: Name } : back)
            // Backlog      
          );

          newTodo = newBacklog.filter((back) => back.id === id)[0];
          // Todo        newTodo 
          // Backlog    Backlog  
          return newBacklog;
        });
        setTodo((oldTodoList) => [...oldTodoList, newTodo]);
        // Todo       setTodo 
        setchecked(!checked);
      } else if (name === "Todo") {
        //   Todo 
        if (job.owner === Name) {
          //   owner    
          setDoing((oldTodoList) => [
            //  Todo   Doing  
            ...oldTodoList,
            Todo.filter((back) => back.id === id)[0],
          ]);
          setTodo((todos) => todos.filter((back) => back.id !== id)); // Todo 
        } else {
          toast({
            title: "Owner ",
            description: `${job.owner}  todo.`,
            status: "error",
            duration: 2000,
            isClosable: true,
            position: "bottom-right",
          });
        }
      } else if (name === "Doing") {
        //   Doing 
        if (job.owner === Name) {
          //   owner    

          setDone((oldTodoList) => [
            ...oldTodoList,
            Doing.filter((back) => back.id === id)[0],
          ]);
          setDoing((doing) => doing.filter((back) => back.id !== id));
          // setchecked(!checked);
        } else {
          toast({
            title: "Owner ",
            description: `${job.owner}  todo.`,
            status: "error",
            duration: 2000,
            isClosable: true,
            position: "bottom-right",
          });
        }
      } else {
        setchecked(!checked);
      }
    } else {
      if (name !== "Backlog") {
        setchecked(!checked);
      }
    }
  };

  const delClick = (id: number) => {
    if (name === "Done") {
      if (job.owner === Name) {
        setDoing((oldTodoList) => [
          ...oldTodoList,
          Done.filter((back) => back.id === id)[0],
        ]);
        setDone((done) => done.filter((back) => back.id !== id));
      } else {
        toast({
          title: "Owner ",
          description: `${job.owner}  todo.`,
          status: "error",
          duration: 2000,
          isClosable: true,
          position: "bottom-right",
        });
      }
    } else if (name === "Doing") {
      if (job.owner === Name) {
        setTodo((oldTodoList) => [
          ...oldTodoList,
          Doing.filter((back) => back.id === id)[0],
        ]);
        setDoing((doing) => doing.filter((back) => back.id !== id));
      } else {
        toast({
          title: "Owner ",
          description: `${job.owner}  todo.`,
          status: "error",
          duration: 2000,
          isClosable: true,
          position: "bottom-right",
        });
      }
    } else if (name === "Todo") {
      if (job.owner === Name) {
        toast({
          title: "Backlog ",
          description: "   Todo  Backlog   .",
          status: "error",
          duration: 2000,
          isClosable: true,
          position: "bottom-right",
        });
      } else {
        toast({
          title: "Owner ",
          description: `${job.owner}  todo.`,
          status: "error",
          duration: 2000,
          isClosable: true,
          position: "bottom-right",
        });
      }
    } else {
      setBacklog((backlog) => backlog.filter((back) => back.id !== id));
    }
  };

  return (
    <Flex h="60px" _hover={{ backgroundColor: "rgba(255,255,255,0.1)" }}>
      <Checkbox
        colorScheme="blackAlpha"
        marginLeft="4%"
        color="blackAlpha.700"
        h="60px"
        w="80%"
        isChecked={checked}
        fontWeight="bold"
        onChange={() => {
          ClickList(job.id);
        }}>
        {name === "Backlog"
          ? `${job.title}`
          : `${job.title} /  : ${job.owner}`}
      </Checkbox>
      <Spacer />
      {name === "Backlog" ? (
        <div></div>
      ) : (
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          as="button"
          w="15%"
          h="60px"
          paddingBottom="1%"
          marginRight="1%"
          onClick={() => delClick(job.id)}>
          
        </Box>
      )}
    </Flex>
  );
}

export default React.memo(List);
