import { useEffect, useState, useMemo } from "react";
import { useRecoilState, useRecoilValue } from "recoil";
import Card from "../components/card/Card";
import { throttle } from "lodash";

import { Grid, GridItem, Box } from "@chakra-ui/react";
import {
  NameState,
  DoingState,
  BacklogState,
  TodoState,
  DoneState,
} from "../components/atom";
import { getPostList, todoType, firstList } from "../components/card/back";

function Main() {
  let Name = useRecoilValue(NameState);
  let [Backlog, setBacklog] = useRecoilState(BacklogState);
  let Doing = useRecoilValue(DoingState);
  let Todo = useRecoilValue(TodoState);
  let Done = useRecoilValue(DoneState);

  const [page, setPage] = useState<number>(11);
  const [posts, setPosts] = useState<todoType[]>(firstList());
  let count = 0;

  const handleScroll = useMemo(
    () =>
      throttle(() => {
        const { innerHeight } = window;
        //    (  )

        const { scrollHeight } = document.body;
        //     ( )

        const { scrollTop } = document.documentElement;
        //  

        const wait = (sec: number) => {
          let start = Date.now(),
            now = start;
          while (now - start < sec * 1000) {
            now = Date.now();
          }
        };

        if (Math.round(scrollTop + innerHeight) >= scrollHeight) {
          // scrollTop innerHeight   scrollHeight ,    .
          setPosts((posts) => posts.concat(getPostList(page + 1)));

          //     posts  .

          setPage((prevPage: number) => prevPage + 1);
          //  state   1 .
          // wait(0.7);
          // count++;
        }
      }, 100000),
    [page, posts]
  );

  useEffect(() => {
    window.addEventListener("scroll", handleScroll, true);
    //   handleScroll   .
    return () => {
      window.removeEventListener("scroll", handleScroll, true);
      //    ,   .
      console.log(posts);
    };
  }, [handleScroll]);

  useEffect(() => {
    setBacklog(posts);
  }, [posts]);

  return (
    <Box
      bg="blackAlpha.200"
      style={{
        width: "100%",
        height: "100vh",
      }}>
      <Box
        w="95%"
        h="30px"
        textAlign="end"
        paddingTop="10px"
        fontSize="17px"
        fontWeight="bold"
        color="red.800">
        {`   : ${Name}`}
      </Box>
      <Box
        fontSize="60"
        textAlign="center"
        w="100%"
        h="110px"
        paddingBottom="20px"
        color="red.600">
        Todo List
      </Box>
      <Grid
        h="80%"
        templateColumns="repeat(4, 1fr)"
        gap={10}
        marginLeft="5%"
        marginRight="5%"
        paddingTop="1%">
        <GridItem w="100%" h="90%" bg="#FF8A73" overflow="hidden">
          <Card name="Backlog" todos={Backlog} />
        </GridItem>
        <GridItem w="100%" h="90%" bg="#FF8A73">
          <Card name="Todo" todos={Todo} />
        </GridItem>
        <GridItem w="100%" h="90%" bg="#FF8A73">
          <Card name="Doing" todos={Doing} />
        </GridItem>
        <GridItem w="100%" h="90%" bg="#FF8A73">
          <Card name="Done" todos={Done} />
        </GridItem>
      </Grid>
    </Box>
  );
}

export default Main;
